import React from "react";
import styled from "styled-components";
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";

import "react-vertical-timeline-component/style.min.css";
import "../../assets/style.scss";

import timelineElements from "./TimeLineData";
const TimeLine = () => {
  return (
    <QualsContainer>
      <Wrapper>
        <Title>ROADMAP</Title>
        <video autoPlay muted loop className="videoArea">
          <source src="./assets/Roadmap.mp4" type="video/mp4" />
        </video>
        <VerticalTimeline>
          {timelineElements.map((element) => {
            return (
              <VerticalTimelineElement
                key={element.key}
                date={element.date}
                dateClassName="date"
                iconStyle={{ color: "red" }}
              >
                {" "}
                <CardWrapper>
                  <DetailsWrapper>
                    <h3 className="vertical-timeline-element-title">
                      {" "}
                      {element.title}{" "}
                    </h3>{" "}
                    <h5 className="vertical-timeline-element-subtitle">
                      {" "}
                      {element.location}{" "}
                    </h5>{" "}
                    <p id="description">{element.description}</p>{" "}
                  </DetailsWrapper>
                </CardWrapper>
              </VerticalTimelineElement>
            );
          })}
        </VerticalTimeline>
      </Wrapper>
    </QualsContainer>
  );
};

const QualsContainer = styled.div`
  width: 100%;
  min-height: 100vh;
  margin-top: 100px;
`;
const Wrapper = styled.div`
  width: "500px";
  height: "400px";
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 38px 25px;
`;
const Title = styled.h2`
  font-family: "Josefin Sans", sans-serif;
  font-size: 40px;
  font-style: normal;
  text-align: center;
  font-weight: 700;
  line-height: 50px;
  letter-spacing: 0.05em;
  text-align: left;
  color: white;
  text-align: center;
`;

const CardWrapper = styled.div`
  display: flex;
  justify-content: space-evenly;
`;
const ImageWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
  text-align: center;
  img {
    width: 180px;
    height: 180px;
  }
`;

const DetailsWrapper = styled.div`
  padding: 25px 25px;
  width: 70%;
  height: 100%;
`;

export default TimeLine;
